@import url(https://fonts.googleapis.com/css?family=Kreon:300,700);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
	margin: 0;
	font-family: 'Kreon', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.pokemon {
	max-height: 80vh !important;
	transition: transform .7s ease-in-out;
}

.pokemon:hover {
	transform: scale(0.8);
}

.intro-title {
	font-family: 'Abril Fatface', cursive !important;
	text-shadow: 3px 5px 2px #474747 !important;
  padding-top:30% !important;
}

#video {
	position: fixed;
	z-index: -1;
	width: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

